/* Checkbox helpers */
.checkbox {
	margin-bottom: 30px;
}

.checkbox-hidden {
	position: absolute;
	opacity: 0;
}

.checkbox-label {
	margin: 0;
	padding-left: 0;
	min-height: inherit;
	width: 100%;
	display: flex;
	align-items: center;
	vertical-align: middle;
	padding-top: 0;
	cursor: pointer;
	text-transform: none;
}

.checkbox-box {
	position: relative;
	width: 25px;
	height: 25px;
	border: 2px solid #000000;
	cursor: pointer;
	display: inline-block;
	vertical-align: middle;
	margin-right: 10px;
	margin-left: 0;
}

.checkbox-text {
	display: block;
	position: relative;
	cursor: pointer;
	text-align: left;
	vertical-align: middle;
	color: #000000;
	font-size: 16px;
	font-size: 1rem;
	text-transform: uppercase;
	margin-left: 0;
}

.checkbox-hidden:checked + .checkbox-label .checkbox-box:after {
	content: '';
	display: block;
	position: absolute;
