		-webkit-transition: opacity 400ms ease-out;
		-moz-transition: opacity 400ms ease-out;
		transition: opacity 400ms ease-out;
		opacity: 0;
	}
	.tab-content-item-md.is-active {
		visibility: visible;
		overflow: visible;
		height: auto;
		-webkit-transition: opacity 400ms ease-out;
		-moz-transition: opacity 400ms ease-out;
		transition: opacity 400ms ease-out;
		opacity: 1;
	}
}

@media (max-width: 770px) {
	.tab-content-item-sm {
		visibility: hidden;
		overflow: hidden;
		height: 0;
		-webkit-transition: opacity 400ms ease-out;
		-moz-transition: opacity 400ms ease-out;
		transition: opacity 400ms ease-out;
		opacity: 0;
	}
	.tab-content-item-sm.is-active {
		visibility: visible;
		overflow: visible;
		height: auto;
		-webkit-transition: opacity 400ms ease-out;
		-moz-transition: opacity 400ms ease-out;
		transition: opacity 400ms ease-out;
		opacity: 1;
	}
}

/* Pagination style 1 */
.pagination {
	display: block;
	width: 100%;
	margin-top: 20px;
	text-align: center;
}

.pagination-list-icon {
	display: inline-block;
	vertical-align: middle;
	width: 50px;
	height: 50px;
	line-height: 50px;
	font-weight: 400;
	margin-right: -4px;
	color: #830055;
	font-size: 24px;
	font-size: 1.5rem;
}

.pagination-list-icon:hover {
	color: #500034;
}

.pagination-list-icon.is-disabled {
	color: #000000;
}

.pagination-text {
	display: inline-block;
	vertical-align: middle;
	color: #000000;
	font-family: "Montserrat", Arial, sans-serif;
	font-weight: 700;
	line-height: 1.2;
	margin: 0 15px 0 10px;
	font-size: 16px;
	font-size: 1rem;
}

.pagination-input {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	width: 50px;
	height: 50px;
	font-family: "Montserrat", Arial, sans-serif;
	font-weight: 700;
	border-radius: 5px;
	color: #000000;
	border: 0;
	box-shadow: none;
	font-size: 16px;
	font-size: 1rem;
	padding: 0 5px;
	margin-right: -4px;
	background-color: #f7f7f7;
	outline: none;
}

/* Pagination style 2 */
.pagination-list {
	display: block;
	width: auto;
	margin: 0;
	text-align: center;
	margin-top: 30px;
}

.pagination-list-item {
	display: inline-block;
	vertical-align: middle;
}

.pagination-list-item:hover .pagination-list-link, .pagination-list-item.is-active .pagination-list-link {
	color: #ffffff;
	background-color: #830055;
}

.pagination-list-link {
	display: block;
	position: relative;
	width: 40px;
	height: 40px;
	line-height: 40px;
	font-weight: 800;
	text-align: center;
	color: #000000;
	border-radius: 100%;
	text-transform: uppercase;
	font-size: 16px;
	font-size: 1rem;
}

/* Breadcrumbs block */
.breadcrumbs-list {
	margin-bottom: 30px;
}

.breadcrumb-list-item {
	display: inline-block;
	vertical-align: middle;
}

.breadcrumb-list-item:last-child .breadcrumb-list-link:after {
	content: none;
}

.breadcrumb-list-link {
	display: inline-block;
	vertical-align: middle;
	line-height: 1.2;
	color: #000000;
	font-size: 12px;
	font-size: 0.75rem;
	text-transform: uppercase;
	margin-right: 15px;
	margin-bottom: 10px;
	position: relative;
}

.breadcrumb-list-link:hover {
	color: #830055;
}

.breadcrumb-list-link:after {
	content: '/';
	position: absolute;
	right: -10px;
	top: 2px;
	display: inline-block;
	vertical-align: middle;
	font-size: 12px;
	font-size: 0.75rem;
	line-height: 1;
	color: #c4c3c3;
}

/* Blog block */
.blog-item {
	margin-bottom: 30px;
}

.blog-content {
	display: block;
	overflow: hidden;
	border: 1px solid #f7f7f7;
	border-radius: 20px;
	background-color: #ffffff;
}

.blog-content:hover .blog-title {
	text-decoration: underline;
}

.blog-content:hover img {
	transform: scale(1.05, 1.05);
}

.blog-image-block {
	overflow: hidden;
}

.blog-image-block img {
	transition: transform 400ms ease-out;
}

.blog-text-block {
	padding: 30px 30px;
}

.blog-date {
	display: block;
	margin-bottom: 15px;
	color: #000000;
	font-family: "Open Sans", Arial, sans-serif;
	font-weight: 400;
	font-size: 12px;
	font-size: 0.75rem;
}

/* Map block */
.map-block {
	width: 100%;
	height: 400px;
	background-color: #000000;
	position: relative;
}

.map {
	width: 100%;
	height: 100%;
}

/* Rate block */
.rate-list {
	display: flex;
	align-items: center;
}

.rate-list-item {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 40px;
	height: 38px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-image: url(../images/rate.svg);
	margin-left: 10px;
	cursor: pointer;
}

.rate-list-item.is-active {
	background-image: url(../images/rate-active.svg);
}

.rate-value {
	display: inline-block;
	vertical-align: middle;
	text-align: right;
	margin-top: 10px;
	color: #000000;
	font-family: "Montserrat", Arial, sans-serif;
	font-weight: 700;
	font-size: 28px;
	font-size: 1.75rem;
}

/* Tooltip block */
.tooltip-block {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	z-index: 9;
	margin-left: 5px;
}

.tooltip-block:hover .tooltip-content {
	opacity: 1;
	visibility: visible;
	bottom: 100%;
	transition: bottom 450ms ease 0.4s ease-out;
}

.tooltip-block-left {
	margin-left: auto;
	margin-right: 5px;
}

.tooltip-block-no {
	margin: 0;
}

.tooltip-text {
	display: flex;
	width: 30px;
	height: 30px;
	align-items: center;
	justify-content: center;
	text-align: center;
	font-size: 12px;
	font-size: 0.75rem;
	font-family: "Open Sans", Arial, sans-serif;
	font-weight: 400;
	cursor: pointer;
}

.tooltip-text-small {
	display: flex;
	width: 15px;
	height: 15px;
	align-items: center;
	justify-content: center;
	border: 1px solid #830055;
	border-radius: 100px;
}

@media (max-width: 770px) {
	.tooltip-text-small {
		padding-top: 1px;
	}
}

.tooltip-text-purple:hover .tooltip-text-small {
	color: #ffffff;
	background-color: #830055;
}

.tooltip-text-purple .tooltip-text-small {
	color: #830055;
	border-color: #830055;
}

.tooltip-text-purple + .tooltip-content {
	background-color: #830055;
}

.tooltip-text-purple + .tooltip-content:after {
	border-color: #830055 transparent transparent transparent;
}

.tooltip-content {
	position: absolute;
	bottom: 60%;
	left: 50%;
	opacity: 0;
	visibility: hidden;
	min-width: 190px;
	background-color: #2d5093;
	padding-bottom: 10px;
	padding: 15px 20px;
	border-radius: 25px;
	margin-bottom: 15px;
	transform: translateX(-50%);
	transition: bottom 450ms ease 0.4s ease-out;
}

.tooltip-content:after {
	content: '';
	position: absolute;
	bottom: -10px;
	left: 50%;
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 10px 10px 0 10px;
	border-color: #2d5093 transparent transparent transparent;
	transform: translateX(-50%);
}

.tooltip-content p {
	line-height: 18px;
}

/* Social block */
.social-list-item {
	display: inline-block;
	margin: 0 0 10px;
	vertical-align: middle;
}

.social-list-link {
	display: inline-block;
	width: 40px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	vertical-align: middle;
	color: #000000;
	font-size: 22px;
	font-size: 1.375rem;
}

.social-list-link:hover {
	color: #830055;
}

/* Dropdown block */
.dropdown-block {
	position: relative;
}

@media (min-width: 770px) {
	.dropdown-block:hover .dropdown-content {
		visibility: visible;
		height: auto;
		opacity: 1;
	}
}

.dropdown-block.is-active .dropdown-content {
	visibility: visible;
	height: auto;
	opacity: 1;
}

.dropdown-block .dropdown-trigger {
	cursor: pointer;
}

.dropdown-block .dropdown-trigger:after {
	content: '\e906';
	position: absolute;
	top: 50%;
	right: 0;
	display: block;
	font-family: "project-name";
	line-height: 1;
	opacity: 0.5;
	color: #000000;
	font-size: 16px;
	font-size: 1rem;
	transform: translateY(-50%);
}

.dropdown-block .dropdown-content {
	position: absolute;
	top: 100%;
	right: 0;
	visibility: hidden;
	overflow: hidden;
	width: 100%;
	height: 0;
	opacity: 0;
	background-color: white;
	box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
}

@media (max-width: 770px) {
	.dropdown-block .dropdown-content {
		position: relative;
		top: auto;
		right: auto;
		background-color: transparent;
	}
}

@media (max-width: 770px) {
	.dropdown-block-sm {
		position: relative;
	}
	.dropdown-block-sm.is-active .dropdown-content {
		visibility: visible;
		width: auto;
		height: auto;
		opacity: 1;
	}
	.dropdown-block-sm .dropdown-trigger {
		cursor: pointer;
	}
	.dropdown-block-sm .dropdown-content {
		position: absolute;
		top: 100%;
		right: 0;
		visibility: hidden;
		overflow: hidden;
		width: 0;
		height: 0;
		opacity: 0;
		background-color: white;
	}
}

@media (max-width: 770px) and (max-width: 770px) {
	.dropdown-block-sm .dropdown-content {
		position: relative;
		top: auto;
		right: auto;
		background-color: transparent;
	}
}

@media (max-width: 991px) {
	.dropdown-list {
		text-align: center;
		margin-top: 1em;
		margin-bottom: 1em;
	}
}

.dropdown-list-item {
	display: block;
}

@media (max-width: 991px) {
	.dropdown-list-item {
		display: inline-block;
		vertical-align: middle;
	}
}

.dropdown-list-item.is-active .dropdown-list-link {
	font-family: "Open Sans", Arial, sans-serif;
	font-weight: 700;
	opacity: 1;
}

.dropdown-list-link {
	position: relative;
	display: block;
	padding: 15px 15px;
	line-height: 1.2;
	opacity: 0.5;
	font-size: 16px;
	font-size: 1rem;
	text-decoration: none;
	text-transform: uppercase;
	color: #000000;
}

.dropdown-list-link:hover {
	opacity: 1;
}

/* Table block */
@media (max-width: 770px) {
	.table-responsives {
		overflow-x: auto;
		overflow-y: hidden;
		width: 100%;
		min-height: .01%;
		border: 0 !important;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		background: linear-gradient(to right, white 30%, rgba(255, 255, 255, 0)), linear-gradient(to right, rgba(255, 255, 255, 0), white 70%) 0 100%, radial-gradient(farthest-side at 0% 50%, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)), radial-gradient(farthest-side at 100% 50%, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0)) 0 100%;
		background-repeat: no-repeat;
		background-color: white;
		background-size: 40px 100%, 40px 100%, 14px 100%, 14px 100%;
		background-position: 0 0, 100%, 0 0, 100%;
		background-attachment: local, local, scroll, scroll;
	}
}

