/* Load more content block */
.load-more-block.is-last .load-more {
	height: auto;
}

.load-more-block.is-last .js-load-more-content {
	display: none;
}

.load-more.is-disabled .js-load-more-content {
	display: none;
}

.loading-post {
	z-index: 1;
	display: none;
}

.loading-post.is-active {
	display: inline-block;
}

.load-more-content {
	cursor: pointer;
}

.cookies-bar {
	position: fixed;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 0 20px;
	height: 60px;
	width: 100%;
	background-color: #000000;
	z-index: 99;
	transition: transform 400ms ease-out;
	transform: translateY(100%);
}

.cookies-bar.is-active {
	transform: translateY(0);
}

@media (max-width: 770px) {
	.cookies-bar {
		flex-wrap: wrap;
		padding: 10px 15px 10px;
		height: auto;
	}
}

.share-item {
	display: inline-block;
	vertical-align: middle;
	margin: 0 15px 15px;
}

.file-upload-input {
	position: absolute;
	z-index: -1;
	overflow: hidden;
	width: .1px;
	height: .1px;
	opacity: 0;
}

.file-upload-input + .file-upload-label {
	display: inline-block;
	overflow: hidden;
	margin-bottom: 0;
	cursor: pointer;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.file-upload-output {
	display: block;
	margin-top: 10px;
	line-height: 1;
	color: #000000;
	font-size: 16px;
	font-size: 1rem;
}

.file-upload-remove {
	position: relative;
	display: inline-block;
	width: 30px;
	height: 30px;
	margin-left: 10px;
	cursor: pointer;
	vertical-align: middle;
	top: -2px;
}

.file-upload-remove:before, .file-upload-remove:after {
	position: absolute;
	display: block;
	width: 20px;
	height: 2px;
	content: '';
	background: #000;
	top: 50%;
	left: 50%;
	margin-left: -10px;
	margin-top: -1px;
}

.file-upload-remove:before {
	-webkit-transform: rotate(45deg);
	-moz-transform: rotate(45deg);
	-o-transform: rotate(45deg);
	transform: rotate(45deg);
}

.file-upload-remove:after {
	-webkit-transform: rotate(-45deg);
	-moz-transform: rotate(-45deg);
	-o-transform: rotate(-45deg);
	transform: rotate(-45deg);
}
